import React, { Component } from 'react';

import { Document, Page } from 'react-pdf';

import BreviaireV1 from '../docs/BreviaireV1.pdf';

class Breviaire extends Component {
    render() {

        var dynamicHeight = 'calc(100vh - ' + 206 + 'px)';

        let pdfStyles = {
            maxWidth: '650px',
            width: '100%',
            minHeight: dynamicHeight,
            marginLeft: 'auto',
            marginRight: 'auto'
        }

        let stylePDF = {
            alignSelf:'center'
        }

        return(
            <div class="page-breviaire">
                <div class="page-breviaire-pdf" style={stylePDF}>
                    <object data={BreviaireV1} type="application/pdf" style={pdfStyles}>
                        <p>Le PDF ne s'affiche pas ? Le bréviaire est disponible sur le Drive de l'AMB !</p>
                    </object>
                </div>
            </div>
        );
    }
}

export default Breviaire